.addWork{
	position: relative;
	
	width: 400px;
}
.add__title{
	display: flex;
	justify-content: space-between;
	padding: 0 15px;
	max-width: 100%;
	background-color: rgb(230, 196, 124);
}
.add__info{
	display: flex;
	flex-direction: column;
	padding: 5px 15px;
	align-items: flex-start;
}
.add__title--add{
	font-size: 16px;
	font-weight: 400;
}
.fa-window-close{
	align-items: center;
	display: flex;
	color: rgb(153, 127, 84);
}
label{
	margin-top: 10px;
	font-weight: 500;
}
input.name, .status{
 width: 100%;
 height: 30px;
}
.add__control{
	display: flex;
	width: 100%;
	justify-content: center;
}
.btn__save, .btn__cancel{
	outline: none;
	border: none;
	padding: 10px 25px;
	margin: 10px;
	border-radius: 6px;
	display: flex;
	color: #fff;

}
.btn__save{
	background-color: rgb(236, 160, 17);
}
.btn__cancel{
	background-color: rgb(201, 18, 18);
}
.control__title{
	margin-left: 3px;
}
#status{
	width: 100%;
	height: 40px;
}