.dropdown{
	position: relative;
	display: flex;
	flex-direction: column;
	
}
.dropdown-menu{
	z-index: 10;
	display: none;
	background-color: white;
	position: absolute;
	top:100%;
	border: 1px solid #000;
	padding-top:5px ;
	
	padding-left: 0;
	margin: 0;
	width: 150px;
}
.btnSort{
	padding: 5px 10px;
	height: 40px;
}
.dropdown-menu li{
	border-bottom: 1px solid #000;
}