.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.container{
  display: flex;
	width: 100%;
margin-top: 30px;
	flex-wrap: wrap;
  justify-content: center;
  column-gap: 40px;
}
li{
  list-style: none;
}
a{
  text-decoration: none;
}
.controls{
  text-align: left;
}
.form{
  height: 100px;
  width: 500px;
  display: block;
  margin: 20px auto;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
